      saGetUserName(titletext) {
        var that = this;
        /*
        that.$swal({
            type: 'success',
            title: titletext,
            text: 'Hello brave new world!',
           showCancelButton: true
        });
        */
      
        // var myimg = '<img src="img/Eichel.png" style="width:40px; height: auto;"/>';
        var promise = new Promise(
          function(resolve, reject) {
            var text;
            that.$swal({
              //background: '#fff url(css/images/table.png)',
              //background: "rgba(139,136,133, 0.8)",
              background: "rgba(133,133,133, 0.8)",
              width: 270,
              title:
                '<span style="text-shadow:none;color:blue;font-family: Verdana,Arial,Helvetica;font-size: 18px;font-weight: bold;">' +
                titletext +
                "</span>",
              input: "text",
              inputPlaceholder: that.randStr(5), //"Ich",
              allowOutsideClick: false,
              // icon: "success",
              // iconHtml: myimg,
              showCloseButton: true,
              confirmButtonText: "Start",
              inputAttributes: {
                maxlength: 10,
                color: "black",
                //autocapitalize: 'off',
                spellcheck: "false",
                autocorrect: "off"
              }
            }).then(function(result) {
              if (result.dismiss) {
                var reason = new Error("Abbruch");
                reject(reason); // reject
              } else {
                text = result.value;
                if (text === "") {
                  text = "Ich";
                  //var reason = new Error('Kein Namen eingegeben.');
                  //reject(reason); // reject
                  resolve(text);
                } else {
                  resolve(text);
                }
              }
            });
          } // function(resolve, reject)
        ); // var promise

        return promise;

      
      },
 randStr(len, chars='ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789') {
  let s = '';
  while (len--) s += chars[Math.floor(Math.random() * chars.length)];
  return s;
},
     animateSuits() {
       if (gameStarted) { clearTimeout(animateSuitsTimer); return; }
      // 
        var xs = 0;
        var xe = 0;
        var ys = 0;
        var ye = 0;
        var d = 25; // 1/2 * card width
        var w, h, headeroffset = 0;

        this.ctx.clearRect(0, 0, canvasWidth, canvasHeight);

        xs = 0;
        xe = canvasWidth - 38 - d;
        ys = 0;
        ye = canvasHeight - headeroffset - d;

        w = 50 + 50 * Math.random();
        h = 50 + 50 * Math.random();
        this.ctx.drawImage(imgGras, imgGrasX, imgGrasY, w, h);
        this.ctx.drawImage(
          imgEichel,
          imgEichelX,
          imgEichelY,
          75,
          60 + 15 * Math.random()
        );
        this.ctx.drawImage(
          imgSchell,
          imgSchellX,
          imgSchellY,
          60 + 15 * Math.random(),
          75
        );
        this.ctx.drawImage(imgHerz, imgHerzX, imgHerzY, w, w);

        imgEichelX = imgEichelX + imgEichelvX;
        imgEichelY = imgEichelY + imgEichelvY;
        if (imgEichelY + imgEichelvY > ye || imgEichelY + imgEichelvY < ys) {
          imgEichelvY = -imgEichelvY;
        }
        if (imgEichelX + imgEichelvX > xe || imgEichelX + imgEichelvX < xs) {
          imgEichelvX = -imgEichelvX;
        }
        imgHerzX = imgHerzX + imgHerzvX;
        imgHerzY = imgHerzY + imgHerzvY;
        if (imgHerzY + imgHerzvY > ye || imgHerzY + imgHerzvY < ys) {
          imgHerzvY = -imgHerzvY;
        }
        if (imgHerzX + imgHerzvX > xe || imgHerzX + imgHerzvX < xs) {
          imgHerzvX = -imgHerzvX;
        }
        imgGrasX = imgGrasX + imgGrasvX;
        imgGrasY = imgGrasY + imgGrasvY;
        if (imgGrasY + imgGrasvY > ye || imgGrasY + imgGrasvY < ys) {
          imgGrasvY = -imgGrasvY;
        }
        if (imgGrasX + imgGrasvX > xe || imgGrasX + imgGrasvX < xs) {
          imgGrasvX = -imgGrasvX;
        }
        imgSchellX = imgSchellX + imgSchellvX;
        imgSchellY = imgSchellY + imgSchellvY;
        if (imgSchellY + imgSchellvY > ye || imgSchellY + imgSchellvY < ys) {
          imgSchellvY = -imgSchellvY;
        }
        if (imgSchellX + imgSchellvX > xe || imgSchellX + imgSchellvX < xs) {
          imgSchellvX = -imgSchellvX;
        }
        animateSuitsTimer = setTimeout(this.animateSuits, 100);
     }
    }

}



